
import { defineComponent, reactive, toRefs, SetupContext, onMounted, watch } from "vue";
interface Data {
  msg: boolean | null;
}
export default defineComponent({
  name: "watermark",
  props: {},
  components: {},
  setup(props, ctx: SetupContext) {
    const data: Data = reactive<Data>({ msg: null });
    return {
      ...toRefs(data)
    };
  },
});
